import { FC } from "react";
import {
    Label,
    FormGroup,
    Input,
    Anchor,
    Button,
    Text,
} from "@doar/components";
import { useForm } from "react-hook-form";
import { hasKey } from "@doar/shared/methods";
import {
    StyledWrap,
    StyledTitle,
    StyledDesc,
    StyledDivider,
    StyledBottomText,
    Container,
    Row
} from "./style";



interface SignUpProps{
    handleChange?:any
}


interface IFormValues {
    email: string;
    password: string;
    first_name: string;
    last_name: string;
}

const SigninForm: FC<SignUpProps> = ({handleChange}) => {
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();

    const onSubmit = (data: IFormValues) => {
        console.log(JSON.stringify(data, null));
        handleChange()
    };

    return (
        <StyledWrap>
            <StyledTitle>Create New Account</StyledTitle>
            <StyledDesc>
                It&apos;s free to signup and only takes a minute.
            </StyledDesc>
            <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
                <Container>
                    <Row>
                        <FormGroup mb="20px">
                            <Label display="block" mb="5px" htmlFor="first_name">
                                Firstname
                            </Label>
                            <Input
                                id="first_name"
                                type="text"
                                placeholder="Enter your firstname"
                                feedbackText={errors?.first_name?.message}
                                state={
                                    hasKey(errors, "first_name") ? "error" : "success"
                                }
                                showState={!!hasKey(errors, "first_name")}
                                {...register("first_name", {
                                    required: "First Name is required",
                                    minLength: {
                                        value: 2,
                                        message: "Minimum length is 2",
                                    },
                                })}
                            />
                        </FormGroup>
                    </Row>
                    <Row>
                        <FormGroup mb="20px">
                            <Label display="block" mb="5px" htmlFor="last_name">
                                Lastname
                            </Label>
                            <Input
                                id="last_name"
                                type="text"
                                placeholder="Enter your Lastname"
                                feedbackText={errors?.last_name?.message}
                                state={
                                    hasKey(errors, "last_name") ? "error" : "success"
                                }
                                showState={!!hasKey(errors, "last_name")}
                                {...register("last_name", {
                                    required: "Last Name is required",
                                    minLength: {
                                        value: 2,
                                        message: "Minimum length is 2",
                                    },
                                })}
                            />
                        </FormGroup>
                    </Row>
                </Container>
               <Container size={2}>
                    <Row>
                      <FormGroup mb="20px" >
                          <Label display="block" mb="5px" htmlFor="email">
                              Telephone Number
                          </Label>
                          <Input
                              id="phone_number"
                              type="text"
                              placeholder="Enter your telephone number"
                              feedbackText={errors?.phone_number?.message}
                              state={hasKey(errors, "phone_number") ? "error" : "success"}
                              showState={!!hasKey(errors, "phone_number")}
                              {...register("phone_number", {
                                  required: "Telephone number is required",
                                  pattern: {
                                      value: /[+998]{3}[0-9]{2}[0-9]{8}/i,
                                      message: "invalid phone number",
                                  },
                              })}
                          />
                      </FormGroup>
                    </Row>
                    <Row>
                       <FormGroup mb="20px">
                           <Label display="block" mb="5px" htmlFor="password">
                               Company Email
                           </Label>
                           <Input
                               id="email"
                               type="text"
                               placeholder="Enter your email address"
                               feedbackText={errors?.email?.message}
                               state={hasKey(errors, "email") ? "error" : "success"}
                               showState={!!hasKey(errors, "email")}
                               {...register("email", {
                                   required: "Email is required",
                                   pattern: {
                                       value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                                       message: "invalid email address",
                                   },
                               })}
                           />
                       </FormGroup>
                   </Row>
               </Container>

                   <FormGroup mb="20px">
                       <Text fontSize="12px">
                           By clicking <strong>Create an account</strong> below,
                           you agree to our terms of service and privacy statement.
                       </Text>
                   </FormGroup>

                <Container>
                    <Row sm="100" md="100" lg="100" xl="100">
                        <FormGroup>
                            <Button type="submit" color="brand2" fullwidth>
                                Next
                            </Button>
                        </FormGroup>
                    </Row>
                </Container>


                <StyledBottomText>
                    Don&apos;t have an account?{" "}
                    <Anchor path="/signin">Sign In</Anchor>
                </StyledBottomText>
            </form>
        </StyledWrap>
    );
};

export default SigninForm;



